import React, {PureComponent} from 'react'
import {
  Row,
  Col
} from 'antd'

export default class FooterComponent extends PureComponent {
  render() {
    const {
      database = {},
      templateConfig = {},
      pageNum,
      sum,
      totalSum,
      totalNum,
      sumTraditional,
      totalSumTraditional,
      pagination
    } = this.props
    return (
      <section className="footer">
        {/* 默认版 */}
        {templateConfig.footerType === 'F_OUTSTOCK_01' && (
          <Row className="group">
            <Col span={24} className="item">合计：{database.sum || 0}</Col>
            <Col span={24} className="item">名称：{database.merchantName || ''}</Col>
            <Col span={24} className="item">地址：{database.receiveAddr || ''}</Col>
            <Col span={24} className="item">留言：{database.buyerRemarks || ''}</Col>
            <Col span={24} className="signature">客户确认签字：</Col>
          </Row>
        )}
        {/* 审核人版 */}
        {templateConfig.footerType === 'F_OUTSTOCK_02' && (
          <Row className="group">
            <Col span={24} className="item">合计：{database.sum || 0}</Col>
            <Col span={24} className="item">名称：{database.merchantName || ''}</Col>
            <Col span={24} className="item">地址：{database.receiveAddr || ''}</Col>
            <Col span={24} className="item">留言：{database.buyerRemarks || ''}</Col>
            <Col span={8} className="signature">审核人：</Col>
            <Col span={8} className="signature">验收人：</Col>
            <Col span={8} className="signature">经办人：</Col>
          </Row>
        )}
        {/* 送货人签字版 */}
        {templateConfig.footerType === 'F_OUTSTOCK_03' && (
          <Row className="group">
            <Col span={24} className="item">合计：{database.sum || 0}</Col>
            <Col span={24} className="item">名称：{database.merchantName || ''}</Col>
            <Col span={24} className="item">地址：{database.receiveAddr || ''}</Col>
            <Col span={24} className="item">留言：{database.buyerRemarks || ''}</Col>
            <Col span={24} className="signature">客户确认签字：</Col>
            <Col span={24} className="signature">送货人签字：</Col>
          </Row>
        )}
        {/* 备注版 */}
        {templateConfig.footerType === 'F_OUTSTOCK_04' && (
          <Row className="group">
            <Col span={24} className="item">留言：{database.buyerRemarks || ''}</Col>
            <Col span={12} className="signature">客户确认签字：</Col>
            <Col span={6} className="signature">负责客户经理：</Col>
            <Col span={6} className="signature">项目经理：</Col>
          </Row>
        )}
        {/* 备注版 */}
        {templateConfig.footerType === 'F_OUTSTOCK_05' && (
          <Row className="group">
            <Col span={24} className="item">不含税金额合计：{database.realTotalPriceWithoutTax || ''}</Col>
            <Col span={24} className="item">税额合计：{database.taxTotalPrice || ''}</Col>
            <Col span={24} className="item">价税合计：{database.realTotalPrice || ''}</Col>
            <Col span={24} className="item">名称：{database.merchantName || ''}</Col>
            <Col span={24} className="item">地址：{database.receiveAddr || ''}</Col>
            <Col span={24} className="item">留言：{database.buyerRemarks || ''}</Col>
            <Col span={24} className="signature">客户确认签字：</Col>
          </Row>
        )}
        {/* 经办人签字版 */}
        {templateConfig.footerType === 'F_OUTSTOCK_06' && (
          <Row className="group">
            <Col span={24} className="item">合计：{database.sum || 0}</Col>
            <Col span={24} className="item">名称：{database.merchantName || ''}</Col>
            <Col span={24} className="item">地址：{database.receiveAddr || ''}</Col>
            <Col span={24} className="item">留言：{database.buyerRemarks || ''}</Col>
            <Col span={8} className="signature">送货人员：</Col>
            <Col span={8} className="signature">经办人员：</Col>
            <Col span={8} className="signature">验收人员：</Col>
          </Row>
        )}
        {/* 分页汇总版 */}
        {templateConfig.footerType === 'F_OUTSTOCK_07' &&
          <Row className="group">
            <Col span={12} className="item">合计金额（大写）：{sumTraditional || database.sumTraditional || ''}</Col>
            <Col span={12} className="item">{sum || database.sum || 0}</Col>
            {totalNum === pageNum ? <Col span={12} className="item">合计总金额（大写）：{totalSumTraditional || database.totalSumTraditional || ''}</Col> : null}
            {totalNum === pageNum ? <Col span={12} className="item">{totalSum || database.totalSum || 0}</Col> : null}
            <Col span={8} className="item">出货仓管确认签字：</Col>
            <Col span={8} className="item">承运方确认签字：</Col>
            <Col span={8} className="item">客户确认签字：</Col>
          </Row>}
        {/* 团购专用版 */}
        {templateConfig.footerType === 'F_OUTSTOCK_08' && (
          <Row className="group">
            <Col span={24} className="item">合计：{database.sum || 0}</Col>
            <Col span={8} className="item">出货仓管确认签字：</Col>
            <Col span={8} className="item">承运方确认签字：</Col>
            <Col span={8} className="item">客户确认签字：</Col>
          </Row>
        )}
      </section>
    )
  }
}



