import React, {PureComponent} from 'react'
import {
  Row,
  Col
} from 'antd'
import moment from 'moment'

export default class HeaderComponent extends PureComponent {
  render() {
    const {
      database = {},
      templateConfig = {},
      pageNum,
      totalNum,
      receiveAddr,
      receivePhone,
      requireDeliveryTime,
      pagination
    } = this.props

    return (
      <section className="header">
        {/* 默认版 */}
        {templateConfig.headerType === 'H_OUTSTOCK_01' && (
          <Row className="group">
            <Col span={24} className="item">订单号：{database.orderNo || ''}</Col>
            <Col span={12} className="item">客户信息：{database.receiveAddr || ''}</Col>
            <Col span={12} className="item">出货门店：{database.merchantName || ''}</Col>
            <Col span={12} className="item">客户订货时间：{database.orderTime ? moment(database.orderTime).format('YYYY-MM-DD HH:mm:ss') : ''}</Col>
            <Col span={12} className="item">客户提货时间：{database.requireDeliveryTime || ''}</Col>
          </Row>
        )}
        {/* 客户号版 */}
        {templateConfig.headerType === 'H_OUTSTOCK_02' && (
          <Row className="group">
            <Col span={12} className="item">订单号：{database.orderNo || ''}</Col>
            <Col span={12} className="item">出货门店：{database.merchantName || ''}</Col>
            <Col span={12} className="item">客户信息：{database.receiveAddr || ''}</Col>
            <Col span={12} className="item">客户号：{database.sapCusCode || ''}</Col>
            <Col span={12} className="item">客户订货时间：{database.orderTime ? moment(database.orderTime).format('YYYY-MM-DD HH:mm:ss') : ''}</Col>
            <Col span={12} className="item">客户提货时间：{database.requireDeliveryTime || ''}</Col>
          </Row>
        )}
        {/* 传真版 */}
        {templateConfig.headerType === 'H_OUTSTOCK_03' && (
          <Row className="group">
            <Col span={8} className="item">客户信息：</Col>
            <Col span={8} className="item">订单号：{database.orderNo || ''}</Col>
            <Col span={8} className="item">负责经理：</Col>
            <Col span={8} className="item">公司名：{database.receiveAddr || ''}</Col>
            <Col span={8} className="item">部门电话：</Col>
            <Col span={8} className="item">传真：</Col>
            {/*<Col span={12} className="item">出货门店：{database.preferDcName || ''}</Col>*/}
            <Col span={12} className="item">客户联系人：{database.receiveUsername || ''}</Col>
            <Col span={12} className="item">订货时间：{database.orderTime ? moment(database.orderTime).format('YYYY-MM-DD HH:mm:ss') : ''}</Col>
            <Col span={12} className="item">客户联系电话/传真：{database.receivePhone || ''}</Col>
            <Col span={12} className="item">送货时间：{database.requireDeliveryTime}</Col>
            <Col span={12} className="item">送货地址：{database.receiveAddr || ''}</Col>
            <Col span={12} className="item">提货时间：</Col>
          </Row>
        )}
        {/* 分页版 */}
        {templateConfig.headerType === 'H_OUTSTOCK_04' &&
          <Row className="group">
            <Col span={8} className="item">客户：{receiveAddr || database.receiveAddr || ''}</Col>
            <Col span={8} className="item">配送部门：</Col>
            <Col span={8} className="item">{`${pageNum || database.pageNum || 1}/${totalNum || database.totalNum || 1}页`}</Col>
            <Col span={8} className="item">客户经理：</Col>
            <Col span={8} className="item">联系电话：{receivePhone || database.receivePhone || ''}</Col>
            <Col span={8} className="item" style={{textAlign: 'center'}}>送货日期：{requireDeliveryTime || database.requireDeliveryTime || ''}</Col>
          </Row>}
        {/* 团购专用版 */}
        {templateConfig.headerType === 'H_OUTSTOCK_05' && (
          <Row className="group">
            <Col span={18}>
              <Row span={12}>
                <Col span={12} className="item">客户信息：{database.receiveAddr || ''}</Col>
                <Col span={12} className="item">部门：</Col>
              </Row>
              <Row span={12}>
                <Col span={12} className="item">客户订货时间：{database.orderTime ? moment(database.orderTime).format('YYYY-MM-DD HH:mm:ss') : ''}</Col>
                <Col span={12} className="item">客户提货时间：{database.requireDeliveryTime || ''}</Col>
              </Row>
            </Col>
            <Col span={6} className="item">订单编号：{database.orderNo || ''}</Col>
          </Row>
        )}
        {/* 重庆专用版 */}
        {templateConfig.headerType === 'H_OUTSTOCK_06' && (
          <Row className="group">
            <Col span={24}>订单号：{database.orderNo || ''}</Col>
            <Col span={12}>客户信息：{database.receiveAddr || ''}</Col>
            <Col span={12}>出货门店：{database.merchantName || ''}</Col>
            <Col span={12}>联系人：{database.custCon}</Col>
            <Col span={12}>联系电话：{database.conTel}</Col>
            <Col span={12} className="item">客户订货时间：{database.orderTime ? moment(database.orderTime).format('YYYY-MM-DD HH:mm:ss') : ''}</Col>
            <Col span={12} className="item">客户提货时间：{database.requireDeliveryTime || ''}</Col>
          </Row>
        )}
        {templateConfig.headerType === 'H_OUTSTOCK_07' && (
            <Row className="group">
              <Col span={24}>订单号：{database.orderNo || ''}</Col>
              <Col span={12}>客户信息：{database.receiveAddr || ''}</Col>
              <Col span={12}>出货门店：{database.merchantName || ''}</Col>
              <Col span={12}>业务联系人：{database.contactPerson}</Col>
              <Col span={12}>业务联系人电话：{database.phone}</Col>
              <Col span={12} className="item">客户订货时间：{database.orderTime ? moment(database.orderTime).format('YYYY-MM-DD HH:mm:ss') : ''}</Col>
              <Col span={12} className="item">客户提货时间：{database.requireDeliveryTime || ''}</Col>
            </Row>
        )}
      </section>
    )
  }
}



