import React, {PureComponent} from 'react'
import {
  message,
  Button
} from 'antd'
import {Table, Header, Footer} from './components'
import {getData, handleOneDecimalPoint, getQueryString} from 'util'
import {
  API_GET_CMS_OUTBOUND_ORDER_CONFIG_SEARCH,
  API_GET_ORDER_DETAIL_CHECK_OUT,
  API_GET_ORDER_DETAIL_REFUND_CHECK_OUT,
  API_GET_CMS_OUTBOUND_ORDER_PREFER_SEARCH
} from 'api'
import {orderDetail} from './mock/data'
import {pageDetail} from './mock/pageData'
import './style/index.less'

export default class App extends PureComponent {
  constructor() {
    super()

    this.state = {
      isRetreat: getQueryString('isRetreat') || false, // 是否为差异单 // 逆向单
      templateId: getQueryString('templateId') || null, // 模版ID
      dcCode: getQueryString('dcCode') || null, // DC编码
      sapSubCusCode: getQueryString('sapSubCusCode') || null, // 子客户编码
      isPage: getQueryString('pagination') || null,
      templateConfig: {columnList: []}, // 模版配置
      orderNo: null, // 订单id
      database: {}, // 详情数据信息
      orderItems: [], // 商品信息
      orderStatus: false, // 订单状态
      loading: false, // 列表loading
      pagination: false // 是否是分页模板，默认是非分页
    }
  }

  componentWillMount() {
    try {
      this._fetchTemplate().then((res) => {
        try {
          let id = this.props.match ? this.props.match.params.id : null
          if (id) { // 正常请求接口
            this._fetchOrderNo(id)
          } else {
            // 假数据
            this.setState({
              database: orderDetail,
              orderNo: orderDetail.orderNo,
              orderItems: this.state.isPage ? pageDetail.result.pageList : orderDetail.orderItems,
              orderStatus: orderDetail.orderStatus,
              pagination: this.state.isPage
            })
          }
        } catch (e) {
          console.error(e)
        }
      })
    } catch (err) {
      console.error(err)
    }
  }

  /* 获取模版类型 */
  _fetchTemplate = () => {
    const {templateId, dcCode, sapSubCusCode} = this.state
    let params = null
    let api = null
    if (templateId) {
      params = {id: templateId}
      api = API_GET_CMS_OUTBOUND_ORDER_CONFIG_SEARCH
    } else if (dcCode && sapSubCusCode) {
      params = {
        dcCode,
        sapSubCusCode
      }
      api = API_GET_CMS_OUTBOUND_ORDER_PREFER_SEARCH
    }
    if (!params) return (0, this._closeWindow)(message.error('信息异常'))
    return getData({api, searchParams: params}).then(res => {
      if (res && res.message === 'success') {
        let state = {}
        if (templateId) {
          state = {
            templateConfig: res.page.result[0] || {columnList: []}
          }
        } else {
          state = {
            templateId: res.result.id,
            templateConfig: res.result
          }
        }

        this.setState(state)
      }
    })
  }

  /* 打印 */
  handlePrint = () => {
    if (window.print) {
      window.print()
    } else {
      document.execCommand('print')
    }
  }

  /* 关闭窗口 */
  _closeWindow = () => window.setTimeout(() => window.close(), 1000)

  /* 获取订单详情 */
  _fetchOrderNo = id => {
    if (!id) return (0, this._closeWindow)(message.error('订单编号错误'))
    this.setState({
      loading: true
    })

    let api = API_GET_ORDER_DETAIL_CHECK_OUT
    if (this.state.isRetreat) {
      api = API_GET_ORDER_DETAIL_REFUND_CHECK_OUT
    }

    getData({api, urlParams: [id, 'checkout'], searchParams: {templateId: this.state.templateId}}).then(data => {
      if (!data.result) return (0, this._closeWindow)(message.error('订单编号错误'))

      // 一位小数点
      if (data.result.scaleStatus) {
        data.result.orderItems.map(item => {
          item.sendQty = handleOneDecimalPoint(item.sendQty, true)
          item.signQty = handleOneDecimalPoint(item.signQty, true)
          item.refundQty = handleOneDecimalPoint(item.refundQty, true)
          item.originPrice = handleOneDecimalPoint(item.originPrice, true)
          item.promotionPrice = handleOneDecimalPoint(item.promotionPrice, true)
        })
      }

      if (data.result && data.result.pageList != null && data.result.pageList.length > 1) {
        this.setState({
          loading: false,
          pagination: true,
          database: data.result,
          orderNo: data.result.orderNo,
          orderItems: data.result.pageList,
          orderStatus: data.result.orderStatus
        })
      } else {
        this.setState({
          loading: false,
          pagination: false,
          database: data.result,
          orderNo: data.result.orderNo,
          orderItems: data.result.orderItems,
          orderStatus: data.result.orderStatus
        })
      }
    })
  }

  render() {
    const {pagination, templateConfig} = this.state

    return (
      <section className="outboundOrderTemplate">
        <Button className="noprint" type={'primary'} onClick={this.handlePrint}>打印</Button>
        {pagination ? (this.state.orderItems && this.state.orderItems.length && this.state.orderItems.map((item) => <div className="pagination">
          <div className="title">{this.state.database.sapCusName || ''}团购订单</div>
          <Header {...this.state} {...item}/>
          {templateConfig !== undefined && templateConfig.columnList !== undefined && templateConfig.columnList && templateConfig.columnList.length ? <Table {...this.state} {...item}/> : null}
          <Footer {...this.state} {...item}/>
        </div>)) : <div>
          <Button className="noprint" type={'primary'} onClick={this.handlePrint}>打印</Button>
          <div className="title">{this.state.database.sapCusName || ''}团购订单</div>
          <Header {...this.state}/>
          {this.state.templateConfig.columnList && this.state.templateConfig.columnList.length ?
          <Table {...this.state}/> : null}
          <Footer {...this.state}/>
        </div>}
      </section>
    )
  }
}



