import React, {PureComponent} from 'react'
import {
  Table
} from 'antd'

export default class TableComponent extends PureComponent {
  constructor(props) {
    super(props)
  }

  render() {
    const {templateConfig, orderItems} = this.props
    let dataSource = []
    orderItems.map((item, index) => {
      dataSource.push(item)
    })
    let columns = []
    templateConfig.columnList.map(item => {
      let column = {
        width: item.field === 'productName' ? 130 : 50,
        title: item.name,
        key: item.field,
        dataIndex: item.field,
        align: 'center',
        render: (val) => <div>
          {item.field === 'productName' ? <span>
              {/* {val.substring(0, 8)}<br/>{val.length > 8 && val.substring(8, val.length)} */}
              {val}
            </span> : <span>{typeof (val) !== 'object' ? val : ''}</span>
          }
        </div>
      }

      // 限定宽度
      if (item.field === 'sortNum') column.width = 20 // 序号
      if (item.field === 'productCode') column.width = 50 // 商品编码
      if (item.field === 'unit') column.unit = 20 // 单位
      if (item.field === 'taxRate') column.unit = 20 // 税率

      columns.push(column)
    })

    return (
      <Table
        rowKey={record => record.itemNo}
        style={{marginTop: '-1px'}}
        loading={this.props.loading}
        columns={columns}
        dataSource={dataSource}
        pagination={false}
        bordered
        size="middle"
      />
    )
  }
}



